          transform: translateY(-50%);
  -ms-transform: translateY(-50%);
}

.myName {
  font-size: 7vw;
  font-weight: 600;
  font-family: "Righteous", cursive;
  color: #757575;
}

.myProfession {
  font-size: 2vw;
