  font-weight: 600;
  margin-top: -3%;
  color: #e2257d;
}

.myIntro {
  font-size: 1.2vw;
  color: #a9a9a9;
  padding: 0% 8%;
}

.buttonContact {
  font-size: 1.7vw;
  margin-top: 4%;
  border-radius: 0.5vw;
  cursor: pointer;
  color: white;
  border: none;
  padding: 2%;
  font-family: "Spinnaker", sans-serif;
  -webkit-transition: font-size 0.3s, box-shadow 0.3s;
  transition: font-size 0.3s, box-shadow 0.3s;
  background-image: -webkit-linear-gradient(
    left,
    #fd898e,
    #f97a91,
    #f26c96,
    #e95f9d,
    #dd54a5
  );
  background-image: linear-gradient(
    to right,
    #fd898e,
    #f97a91,
    #f26c96,
    #e95f9d,
    #dd54a5
  );
}

.buttonContact:hover {
  font-size: 1.8vw;
  box-shadow: 0px 30px 15px -20px #f4b5da;
}

.downloadResume {
  font-size: 1.2vw;
  margin-top: 1%;
  text-decoration: none;
  color: gray;
  -webkit-transition: border-bottom 0.3s;
  transition: border-bottom 0.3s;
  border-bottom: 1px solid rgb(211, 211, 211, 0);
}

.downloadResume:hover {
  border-bottom: 1px solid rgb(211, 211, 211, 1);
  cursor: pointer;
}

