.navBar {
  background-color: #30364a;
  box-shadow: 2px 0px 20px grey;
  width: 5vw;
  padding: 0% 0.3%;
  position: relative;
}

.hamburgerButton {
  font-size: 4vw;
}

.sideBarIdentifier {
  top: 5px;
  color: #e2257d;
  position: relative;
  -webkit-animation-name: example;
          animation-name: example;
  -webkit-animation-duration: 1s;
          animation-duration: 1s;
  -webkit-animation-iteration-count: infinite;
          animation-iteration-count: infinite;
}

@-webkit-keyframes example {
  0% {
    left: 0px;
  }
  25% {
    left: 5px;
  }
  50% {
    left: 10px;
  }
  75% {
    left: 5px;
  }
  100% {
    left: 0px;
  }
}

@keyframes example {
  0% {
    left: 0px;
  }
  25% {
    left: 5px;
  }
  50% {
    left: 10px;
  }
  75% {
    left: 5px;
  }
  100% {
    left: 0px;
  }
}

.navBarItems {
  position: relative;
  top: 40%;
  -webkit-transform: translateY(-50%);
          transform: translateY(-50%);
  -ms-transform: translateY(-50%);
}

.icon {
  margin-top: 10px;
  width: 4vw;
  height: 8vh;
}

.menuItem {
  text-decoration: none;
  line-height: 1vh;
  margin-top: 1vh;
  cursor: pointer;
  color: #afa7a7;
  -webkit-transition: color 0.3s;
  transition: color 0.3s;
}

.menuItem:hover {
  color: white;
}

.menuIcon {
  font-size: 2vw;
}

.menuName {
  font-size: 0.9vw;
}

.active {
  color: white !important;
  cursor: no-drop;
}

/* All media queries */

/* for tabs and ipads*/
@media screen and (max-width: 1050px) and (min-width: 601px) {
  .navBar {
    box-shadow: 2px 0px 20px grey;
    width: 14vw;
